import './index.scss';

import React, { useContext, } from 'react';
import { ThemeContext, } from '../theme';

//import { useLocalization, LocalizedLink, LocalesList, } from 'gatsby-theme-i18n';
import { useTranslation, } from 'react-i18next';
import Page from '../components/Page';
import CommonButton from '../components/CommonButton';

import ScreenPortfolio from '../screens/ScreenPortfolio';
import ScreenProjects from '../screens/ScreenProjects';
import ScreenBlog from '../screens/ScreenBlog';

//import ImageMain from '../assets/images/main.svg';
import ImageMain from '../assets/images/main.webp';
//import { LinkExternal, } from '../components/Link';
//import { StaticImage } from 'gatsby-plugin-image';


// https://www.gatsbyjs.com/docs/how-to/images-and-media/using-gatsby-plugin-image/
//import { StaticImage } from 'gatsby-plugin-image'

import { useModal, } from 'react-simple-modal-provider';




export default function IndexPage() {
  const { theme, } = useContext(ThemeContext);
  //const { locale, defaultLang, config, } = useLocalization();
  const { t, } = useTranslation('translation');
  const { open: openGetInTouchModal, } = useModal('GetInTouchModal');

  return (
    <Page
      keywords={[
        'developer', 'development',
        'blockchain', 'crypto', 'ethereum', 'web3',
        'nft', 'smart contract', 'token',
      ]}
    >
      <div id={`front ${theme}`} style={{ minHeight: '95vh', }}>
        <main className='main'>
          <section id='home' className='section section-start'>
            <div className='container'>
              <div className='row'>
                <div className='col-12 col-md-7 col-lg-6 col-xl-6 relative'>
                  <div className='pre-title'>
                    <span>{t('Aleksandr Ivannikov')}</span>
                  </div>
                  <h1 className=''>
                    {t('ProjectSlogan')}
                  </h1>
                  <p className='desc'>{t('Introduction')}</p>
                  <CommonButton
                    variant='gradient'
                    size='static'
                    className=''
                    onClick={openGetInTouchModal}
                  >{t('Get in Touch')}</CommonButton>
                </div>
                <div className='col-12 col-md-5 col-lg-6 col-xl-6'>
                  <div className='start__image'>
                    <img
                      alt={t('IVANNIKOV.PRO')}
                      title={t('IVANNIKOV.PRO')}
                      src={ImageMain}
                    />
                  </div>
                </div>
              </div>
            </div>
            <br />
          </section>

          <ScreenProjects />

          <ScreenBlog />

          <ScreenPortfolio />
        </main>
      </div>
    </Page>
  );
}


